@use 'abstracts/mixins' as *;

body {
  background: var(--white);
}

.jumbotron {
  padding-top: 134px;

  @include md {
    padding-top: 76px;
  }

  .section-title {
    color: var(--mid-green);
    font-size: 20px;
    margin-bottom: 24px;
    padding-left: 0;
    margin-left: -1px;
    letter-spacing: 2px;

    &::before {
      font-size: 20px;
      margin-right: 4px;

      @include md {
        font-size: 16px;
      }
    }

    @include md {
      font-size: 16px;
      margin-bottom: 22px;
      letter-spacing: 1.6px;
    }
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 30px;
    letter-spacing: 1.5px;
    line-height: 46px;
    color: var(--mid-green);
    font-weight: 300;
    margin-bottom: 67px;
    max-width: 940px;
    margin-left: -2px;

    @include md {
      font-size: 20px;
      letter-spacing: 1px;
      line-height: 28px;
      padding-left: 2px;
    }

    span {
      color: var(--gold);
    }
  }
}

.top-article {
  background: url('./../assets/maisie_mainimage_1.webp') no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 180px;
  padding-bottom: 42px;
  min-height: 734px;
  text-align: center;

  @include md {
    padding-top: 57px;
    min-height: 572px;
  }

  &__logo {
    margin-bottom: 60px;

    @include md {
      margin-bottom: 42px;
    }
  }

  &__title {
    text-transform: uppercase;
    font-size: 28px;
    letter-spacing: 2.8px;
    color: var(--light-grey);
    margin-bottom: 51px;

    @include md {
      font-size: 24px;
      letter-spacing: 2.4px;
      margin-bottom: 48px;
      line-height: 26px;
    }
  }

  &__description {
    font-family: var(--unnamed-font-family-garamond-premier-pro);
    font-weight: 300;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 1.2px;
    color: var(--light-grey);
    max-width: 740px;
    margin: 0 auto 17px auto;

    @include md {
      font-size: 16px;
      line-height: 22px;
      letter-spacing: 0.8px;
      max-width: 246px;
      margin-bottom: 52px;
    }
  }

  &__button-container {
    max-width: 235px;
    margin: 0 auto;
    @include md {
      max-width: 151px;
    }

    .button-outlined {
      @include md {
        padding-left: 11px;
        font-size: 10px;
        letter-spacing: 1px;
        height: 39px;
      }
    }
  }
}

.articles {
  padding-top: 85px;

  @include md {
    padding-top: 51px;
  }

  &-container {
    display: flex;
    flex-wrap: wrap;
    padding: 0 37px;

    @include md {
      padding: 0;
    }
  }

  &__item {
    width: 50%;
    padding: 0 22px;
    margin-bottom: 70px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-self: stretch;

    @include md {
      width: 100%;
      padding: 0 0 0 15px;
      margin-bottom: 44px;
    }

    &::before {
      content: '';
      display: block;
      height: 100%;
      left: 0;
      top: 0;
      width: 2px;
      background: var(--gold);
      position: absolute;
    }

    &__img {
      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &__title {
      text-transform: uppercase;
      color: var(--mid-green);
      font-size: 22px;
      letter-spacing: 2.2px;
      line-height: 38px;
      margin-top: 34px;
      margin-bottom: 58px;

      @include md {
        margin-top: 23px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 1.6px;
        margin-bottom: 33px;
      }
    }

    &__button {
      &-container {
        display: flex;
        align-items: flex-end;
        flex-grow: 1;
        max-width: 235px;

        @include md {
          max-width: 151px;
        }

        .button-outlined {
          @include md {
            padding-left: 11px;
            font-size: 10px;
            letter-spacing: 1px;
            height: 39px;
          }
        }
      }
    }
  }

  &__button-container {
    margin: 17px auto 84px auto;
    max-width: 340px;
    width: 100%;

    @include md {
      max-width: 251px;
      margin-top: 21px;
      margin-bottom: 66px;
    }

    .button-outlined {
      @include md {
        padding-left: 11px;
        font-size: 10px;
        letter-spacing: 1px;
        height: 39px;
      }
    }
  }
}
